import Vue from 'vue'
import App from './App.vue'
import Axios from 'axios'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import router from '../router/index.js'
import store from './store'
import VueCookie from 'vue-cookie'
import XLSX from 'xlsx'
import { Notification, Message } from 'element-ui'
import globalVariable from './api/globle.js'
import NewMessage from './components/common/index.js'
import { mockXHR } from '../mock'
if (process.env.NODE_ENV === 'development') {
  mockXHR()
}

Vue.prototype.GLOBAL = globalVariable
Vue.config.productionTip = false
Vue.use(ElementUI)
Vue.use(VueCookie)
Vue.use(XLSX)
Vue.use(NewMessage)



// Vue
router.beforeEach((to, from, next) => {
  let self = this;
  if (to.path != '/login' && to.path != '/register'&& to.path != '/test') {
    if (from.path == '/login' && to.path != '/register'&& to.path != '/test') {
      // 
      Axios.post(
        `${sessionStorage.getItem("url")}/user/info1`,
        {
          "username": sessionStorage.getItem("username"),
          "company": sessionStorage.getItem("company")
        },
        {
          headers: {
            "token": sessionStorage.getItem("TOKEN")
          }
        })
        .then(data => {
          sessionStorage.setItem("userId", data.data.userId);
          sessionStorage.setItem("nickName", data.data.nickName);
          sessionStorage.setItem("userType", data.data.userType);
          sessionStorage.setItem("email", data.data.email);
          setTimeout(next(), 2000);
          Message.success('')
        })
    }
    else if (sessionStorage.getItem("TOKEN") == null) {
      // 
      next('/login')
      Message.warning('');
      document.title = '';
    }
    else {
      next();
      document.title = to.meta.title;
    }
  }
  else {
    if (sessionStorage.getItem("TOKEN") != null) {
      // 
      next('/homepage')
      Message.warning('');
      document.title = to.meta.title;
    }
    else {
      next();
      document.title = to.meta.title;
    }
  }
});

// TOKEN
Axios.interceptors.response.use(response => {
  if (response) {
    switch (response.data.code) {
      case 403: //,
        sessionStorage.removeItem("company")
        sessionStorage.removeItem("TOKEN")
        sessionStorage.removeItem("username")
        sessionStorage.removeItem("userId")
        sessionStorage.removeItem("nickName")
        sessionStorage.removeItem("userType")
        sessionStorage.removeItem("email")
        router.replace({ path: "/login" });
        Message.warning('');
    }
  }
  return response;
}, error => {
  return Promise.reject(error.response.data) //
})

new Vue({
  el: '#app',
  router,
  store,
  data() {
    return {
      // global var
         url: "http://127.0.0.1:8000/biubiu", /*  */
      //url: "http://120.76.208.180:8080/warehouse" /* 2 */
      // url: "http://47.96.132.244:8088/warehouse", /*  */
      // url: "http://120.76.208.180:8086/warehouse", /* 2 */
      // url: "", /*  */
    };
  },
  render: h => h(App),
}).$mount('#app')
