<template>
  <div>
    <div class="head">
      <el-row :gutter="20">
        <el-col :span="16">
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/homepage' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>
        </el-col>
        <el-col :span="8">
          <new-message></new-message>
        </el-col>
      </el-row>
    </div>
    
    <div class="steps" style="text-align: left">
      <el-steps :active="stepNum" finish-status="success">
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>
    </div>

    <div class="table" v-if="isStep1">
      <el-table :data="table" style="width: 100%" :row-class-name="tableRowClassName" :cell-style="{'text-align':'center'}" :header-cell-style="{'text-align':'center'}">
        <el-table-column prop="state" label="" width="110"></el-table-column>
        <el-table-column prop="storageId" label="" width="140"></el-table-column>
        <el-table-column prop="userId" label="ID" width="130" v-if="false"></el-table-column>
        <el-table-column prop="userName" label="" width="130"></el-table-column>
        <el-table-column prop="recordId" label="recordId" width="180" v-if="false"></el-table-column>
        <el-table-column prop="opType" label="" width="100"></el-table-column>
        <el-table-column prop="orderType" label="" width="100"></el-table-column>
        <el-table-column prop="orderNumber" label="" width="100"></el-table-column>
        <!-- <el-table-column prop="model" label="" width="100"></el-table-column>
        <el-table-column prop="spec" label="" width="100"></el-table-column> -->
        <el-table-column prop="num" label="" width="80"></el-table-column>
        <el-table-column prop="supplier" label="" width="80"></el-table-column>
        <!-- <el-table-column prop="warehouse" label="I" width="100"></el-table-column>
        <el-table-column prop="warehouseType" label="II" width="100"></el-table-column> -->
        <el-table-column prop="time" label="" width="200"></el-table-column>
        <el-table-column prop="DemandList" label="" width="110">
          <template slot-scope="scope">
            <el-popover
              placement="right"
              width="510"
              trigger="hover">
              <el-table :data="scope.row.DemandList">
                <el-table-column width="100" property="model" label=""></el-table-column>
                <el-table-column width="100" property="spec" label=""></el-table-column>
                <el-table-column width="100" property="warehouse" label="I"></el-table-column>
                <el-table-column width="100" property="state" label="II"></el-table-column>
                <el-table-column width="100" property="nums" label=""></el-table-column>
              </el-table>
              <el-button slot="reference"></el-button>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column prop="remark" label="" width="110">
          <!-- <template slot-scope="scope">
            <el-tooltip class="item" effect="dark" :content="scope.row.remark" placement="bottom">
              <el-button></el-button>
            </el-tooltip>
          </template> -->
        </el-table-column>
        <el-table-column fixed="right" label="" width="120">
          <template slot-scope="scope" v-if="isLock(scope.row)">
            <el-button
              @click="handleLock(scope.row)"
              type="text"
              size="medium"
              icon="el-icon-chat-line-round"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        layout="prev, pager, next"
        :total="totalRow"
        :current-page="page"
        @current-change="handlePageChange"
      ></el-pagination>
    </div>

    <div class="form" v-if="isStep2" style="width: 320px">
      <el-form label-position="left" label-width="100px" :model="form">
        <el-form-item label="" style="width: 354px">
          <el-select v-model="form.company" placeholder="" disabled style="width: 254px">
            <el-option label="" value="sj"></el-option>
            <el-option label="" value="td"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" style="width: 354px">
          <el-input v-model="form.nums" disabled></el-input>
        </el-form-item>
        <el-form-item label="" style="width: 354px">
          <el-popover
            placement="right"
            width="510"
            trigger="hover">
            <el-table :data="form.DemandList">
              <el-table-column width="100" property="model" label=""></el-table-column>
              <el-table-column width="100" property="spec" label=""></el-table-column>
              <el-table-column width="100" property="warehouse" label="I"></el-table-column>
              <el-table-column width="100" property="state" label="II"></el-table-column>
              <el-table-column width="100" property="nums" label=""></el-table-column>
            </el-table>
            <el-button slot="reference" style="width: 254px"></el-button>
          </el-popover>
        </el-form-item>
        <!-- <el-form-item label="" style="width: 354px">
          <el-input v-model="form.warehouse" disabled></el-input>
        </el-form-item> -->
        <el-form-item label="" style="width: 354px">
          <el-link type="primary" href="http://120.76.208.180:8080/warehouse/file/cai_gou_ru_ku.xlsx"  style="width: 254px">Excel</el-link>
        </el-form-item>
        <el-form-item label="" style="width: 320px">
          <el-row>
            <el-col :span="17">
              <!--limit:on-exceed  -->
              <el-upload
                class="upload-demo"
                action=""
                :on-change="handleChange"
                :on-remove="handleRemove"
                :on-exceed="handleExceed"
                :limit="limitUpload"
                accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet,application/vnd.ms-excel"
                :auto-upload="false">
                <el-button size="small" type="primary">.xls.xlsx</el-button>
              </el-upload>
            </el-col>

            <el-col :span="2">
              <el-divider direction="vertical"></el-divider>
            </el-col>

            <el-col :span="5">
              <el-button type="primary" size="small" @click="manualVisible"></el-button>
              <el-dialog title="" :visible.sync="dialogFormVisible" :modal-append-to-body='false'>
                <el-form :model="dataTemp">
                  <el-form-item label="" label-width="130px">
                    <el-input v-model="dataTemp.sn" placeholder="" style="width: 350px" size="small"></el-input>
                  </el-form-item>
                  <el-form-item label="" label-width="130px">
                    <el-select v-model="dataTemp.model" filterable @change="getSpec" placeholder="" style="width: 350px" size="small">
                      <el-option
                        v-for="item in modelOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                      </el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label="" label-width="130px">
                    <el-select v-model="dataTemp.spec" filterable placeholder="" style="width: 350px" size="small">
                      <el-option
                        v-for="item in specOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                      </el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label="" label-width="130px">
                    <el-input v-model="dataTemp.order" placeholder="" style="width: 350px" size="small"></el-input>
                  </el-form-item>
                  <el-form-item label="" label-width="130px">
                    <el-input v-model="dataTemp.caseId" placeholder="" style="width: 350px" size="small"></el-input>
                  </el-form-item>
                  <el-form-item label="MAC" label-width="130px">
                    <el-input v-model="dataTemp.mac" placeholder="" style="width: 350px" size="small"></el-input>
                  </el-form-item>
                  <el-form-item label="" label-width="130px">
                    <el-input v-model="dataTemp.producer" placeholder="" style="width: 350px" size="small"></el-input>
                  </el-form-item>
                  <el-form-item label="" label-width="130px">
                    <el-input v-model="dataTemp.sensor" placeholder="" style="width: 350px" size="small"></el-input>
                  </el-form-item>
                  <el-form-item label="1" label-width="130px">
                    <el-input v-model="dataTemp.version1" placeholder="" style="width: 350px" size="small"></el-input>
                  </el-form-item>
                  <el-form-item label="2" label-width="130px">
                    <el-input v-model="dataTemp.version2" placeholder="" style="width: 350px" size="small"></el-input>
                  </el-form-item>
                  <el-form-item label="3" label-width="130px">
                    <el-input v-model="dataTemp.version3" placeholder="" style="width: 350px" size="small"></el-input>
                  </el-form-item>
                  <el-form-item label="" label-width="130px">
                    <el-input v-model="dataTemp.manufacturer" placeholder="" style="width: 350px" size="small"></el-input>
                  </el-form-item>
                  <el-form-item label="" label-width="130px">
                    <!-- <el-input v-model="dataTemp.guarantee" placeholder="2000-01-01" style="width: 350px" size="small"></el-input> -->
                    <el-date-picker
                      v-model="dataTemp.guarantee"
                      type="date"
                      placeholder=""
                      style="width: 350px"
                      value-format="yyyy-MM-dd"
                      size="small">
                    </el-date-picker>
                  </el-form-item>
                  <el-form-item label="" label-width="130px">
                    <el-input v-model="dataTemp.resolution" placeholder="" style="width: 350px" size="small"></el-input>
                  </el-form-item>
                  <el-form-item label="" label-width="130px">
                    <el-input v-model="dataTemp.addressee" placeholder="" style="width: 350px" size="small"></el-input>
                  </el-form-item>
                  <el-form-item label="" label-width="130px">
                    <el-input v-model="dataTemp.address" placeholder="" style="width: 350px" size="small"></el-input>
                  </el-form-item>
                  <el-form-item label="" label-width="130px">
                    <el-input v-model="dataTemp.remarks" placeholder="" type="textarea" style="width: 350px" size="small"></el-input>
                  </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                  <el-button @click="dialogFormVisible = false"> </el-button>
                  <el-button type="primary" @click="handleAdd"> </el-button>
                </div>
              </el-dialog>
            </el-col>

          </el-row>
        </el-form-item>
        <el-form-item>
          <el-row>
            <el-col :span="11">
              <el-button type="primary" @click="onSubmit" size="small" :loading="btnLoading"></el-button>
            </el-col>
            <el-col :span="4">
              <el-divider direction="vertical"></el-divider>
            </el-col>
            <el-col :span="9">
              <el-button type="primary" @click="back0" size="small"></el-button>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.cnt" disabled style="width: 254px">
            <template slot="prepend"></template>
          </el-input>
        </el-form-item>
      </el-form>
    </div>

    <div class="table2" v-if="isStep2">
      <el-table :data="form.deviceJson" style="width: 100%" :cell-style="{'text-align':'center'}" :header-cell-style="{'text-align':'center'}">
        <el-table-column prop="sn" label="" width="100"></el-table-column>
        <el-table-column prop="model" label="" width="100"></el-table-column>
        <el-table-column prop="spec" label="" width="100"></el-table-column>
        <el-table-column prop="order" label="" width="100"></el-table-column>
        <el-table-column prop="caseId" label="" width="180"></el-table-column>
        <el-table-column prop="mac" label="MAC" width="110"></el-table-column>
        <el-table-column prop="producer" label="" width="100"></el-table-column>
        <el-table-column prop="sensor" label="" width="100"></el-table-column>
        <el-table-column prop="version1" label="1" width="100"></el-table-column>
        <el-table-column prop="version2" label="2" width="100"></el-table-column>
        <el-table-column prop="version3" label="3" width="100"></el-table-column>
        <el-table-column prop="manufacturer" label="" width="110"></el-table-column>
        <el-table-column prop="guarantee" label="" width="100"></el-table-column>
        <el-table-column prop="resolution" label="" width="100"></el-table-column>
        <el-table-column prop="addressee" label="" width="100"></el-table-column>
        <el-table-column prop="address" label="" width="100"></el-table-column>
        <el-table-column prop="remarks" label="" width="180"></el-table-column>
        <el-table-column fixed="right" label="" width="120">
          <template slot-scope="scope">
            <el-button
              @click="handleDelete(scope.$index)"
              type="primary"
              size="small"
              icon="el-icon-chat-line-round"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="complete" v-if="isStep3">
      <div v-if="complete">
        <h2></h2>
      </div>
      <div v-if="!complete">
        <h2></h2>
        <span>{{ errorMsg }} </span>
      </div>
      <el-divider></el-divider>
      <span>
        <el-button type="primary" @click="back"></el-button>
      </span>
    </div>
  </div>
</template>

<script>
import Axios from "axios";
import "element-ui/lib/theme-chalk/display.css";
export default {
  inject: ['reload'],
  data() {
    return {
      company: "undefined",
      nickname: "undefined",
      userType: "undefined",
      userId: "undefined",
      totalRow: 0,
      page: 1,
      table: [],
      limitUpload: 1,
      form: {
        company: '',
        recordId: '',
        nums: '',
        warehouse: '',
        userId: '',
        DemandList: [],
        deviceJson: [],
        cnt: 0,
      },
      file: null,
      fileTemp: null,
      dialogFormVisible: false,
      dataTemp: {
        sn: null,
        model: "",
        spec: "",
        order: "",
        caseId: "",
        mac: "",
        producer: "",
        sensor: "",
        version1: "",
        version2: "",
        version3: "",
        manufacturer: "",
        guarantee: "",
        resolution: "",
        addressee: "",
        address: "",
        remarks: null
      },
      stepNum: 0,
      isStep1: true,
      isStep2: false,
      isStep3: false,
      complete: false,
      errorMsg: "",
      optionNums: 0,
      modelOptions: [],
      specOptions: [],
      modelList: [],
      btnLoading: false,
    };
  },
  beforeMount() {
    this.company = sessionStorage.getItem("company");
    this.nickname = sessionStorage.getItem("nickname");
    this.userType = sessionStorage.getItem("userType");
    this.userId = sessionStorage.getItem("userId");
    let self = this;
    Axios.all([self.initTable(), self.initModelList()])
      .then(
        Axios.spread((data, data0) => {
          self.totalRow = data.data.total;
          self.table = data.data.list;
          self.optionNums = data0.data.nums;
          self.modelList = data0.data.modelList;
          for (let i = 0; i < data0.data.nums; i++) {
            self.modelOptions.push({
              value: data0.data.modelList[i].model,
              label: data0.data.modelList[i].model
            })
          }
          self.modelOptions.push({
            value: '',
            label: ''
          })
          setTimeout(() => {
            for (let i = 0; i < self.table.length; i++) {
              Axios.post(
                `${sessionStorage.getItem("url")}/user/info`,
                {
                  "username": self.table[i].userId,
                  "company": sessionStorage.getItem("company")
                },
                {
                  headers: {
                    "token": sessionStorage.getItem("TOKEN")
                  }
                })
                .then(data2 => {
                  self.$nextTick(() => {
                    self.table[i].nickname = data2.data.nickName;
                  })
                })
            }
          }, 100);
        })
      )
      .catch(err => {
        console.log(err);
        if (err.status == 403) {
          sessionStorage.removeItem("company")
          sessionStorage.removeItem("TOKEN")
          sessionStorage.removeItem("username")
          sessionStorage.removeItem("userId")
          sessionStorage.removeItem("nickName")
          sessionStorage.removeItem("userType")
          sessionStorage.removeItem("email")
          self.$router.replace({ path: "/login" });
          self.$message({
            message: '',
            type: "warning"
          });
        } else {
          self.$message.error(err);
        }
      });
  },
  methods: {
    getSpec() {
      // console.log("getSpec")
      if (this.dataTemp.model == "" || this.dataTemp.model == "") {
        this.specOptions = [{
          value: '',
          label: ''
        }]
      }
      else {
        for (let i = 0; i < this.optionNums; i++) {
          if (this.modelList[i].model == this.dataTemp.model) {
            this.specOptions = [];
            for (let j = 0; j < this.modelList[i].spec.length; j++) {
              this.specOptions.push({
                value: this.modelList[i].spec[j],
                label: this.modelList[i].spec[j]
              })
            }
            this.specOptions.push({
              value: '',
              label: ''
            })
            return;
          }
        }
        this.specOptions = [{
          value: '',
          label: ''
        }]
      }
    },
    initTable() {
      return Axios.post(
        `${this.$root.url}/storage/list${sessionStorage.getItem("companyUrl")}`,
        {
          rowsPerPage: 10,
          page: this.page,
          userId: sessionStorage.getItem("userId"),
          type: sessionStorage.getItem("userType")
        },
        {
          headers: {
            token: sessionStorage.getItem("TOKEN")
          }
        }
      );
    },
    initModelList() {
      return Axios.post(
        `${this.$root.url}/tool/modelList`,
        {},
        {
          headers: {
            token: sessionStorage.getItem("TOKEN")
          }
        }
      );
    },
    tableRowClassName({row, rowIndex}) {
      // console.log(rowIndex)
      if (row.state == "") {
        return 'warning-row';
      } else if (row.state == "") {
        return 'success-row';
      }
      return '';
    },
    isLock(msg) {
      if (msg.state == "" || msg.state == "") {
        return true;
      }
      return false;
    },
    lockSubmit(msg) {
      let self = this;
      Axios.post(`${this.$root.url}/storage/lock`, 
        {
          company: self.company,		
          num: msg.num,
          recordId: msg.recordId,
          warehouse: msg.warehouse
        },
        {
          headers: {
            token: sessionStorage.getItem("TOKEN")
          }
        }
      )
      .then(data => {
        if (data.data.status == 2000) {
          self.form.company = self.company;
          self.form.recordId = msg.recordId;
          self.form.nums = msg.num;
          self.form.DemandList = msg.DemandList;
          self.form.userId = msg.userId;
          // 
          self.$message({
            message: "",
            type: "success"
          });
          self.stepNum = 1;
          self.isStep1 = false;
          self.isStep2 = true;
        }
        else {
          self.$message({
            message: "",
            type: "warning"
          });
        }
      })
      .catch(err => {
        console.log(err);
        if (err.status == 403) {
          sessionStorage.removeItem("company")
          sessionStorage.removeItem("TOKEN")
          sessionStorage.removeItem("username")
          sessionStorage.removeItem("userId")
          sessionStorage.removeItem("nickName")
          sessionStorage.removeItem("userType")
          sessionStorage.removeItem("email")
          self.$router.replace({ path: "/login" });
          self.$message({
            message: '',
            type: "warning"
          });
        } else {
          self.$message.error(err);
        }
      });
    },
    handleLock(row) {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.lockSubmit(row)
          // console.log(row)
        })
        .catch(() => {});
    },
    handlePageChange(e) {
      let self = this;
      this.page = e;
      Axios.post(
        `${this.$root.url}/storage/list${sessionStorage.getItem("companyUrl")}`,
        {
          rowsPerPage: 10,
          page: e,
          userId: sessionStorage.getItem("userId"),
          type: sessionStorage.getItem("userType")
        },
        {
          headers: {
            token: sessionStorage.getItem("TOKEN")
          }
        }
      )
      .then(data => {
        self.totalRow = data.data.total;
        self.table = data.data.list;
      })
      .catch(err => {
        console.log(err);
        if (err.status == 403) {
          sessionStorage.removeItem("company")
          sessionStorage.removeItem("TOKEN")
          sessionStorage.removeItem("username")
          sessionStorage.removeItem("userId")
          sessionStorage.removeItem("nickName")
          sessionStorage.removeItem("userType")
          sessionStorage.removeItem("email")
          self.$router.replace({ path: "/login" });
          self.$message({
            message: '',
            type: "warning"
          });
        } else {
          self.$message.error(err);
        }
      });
    },
    onSubmit() {
      this.btnLoading = true;
      let self = this;
      if (!this.form.deviceJson.length) {
        // 
        this.$message({
          message: '',
          type: 'warning'
        });
        this.btnLoading = false;
        return;
      }
      if (this.form.deviceJson.length != this.form.nums) {
        // 
        this.$message({
          message: '',
          type: 'warning'
        });
        this.btnLoading = false;
        return;
      }
      Axios.post(
        `${this.$root.url}/storage/confirm`,
        {
          company: self.form.company,
          recordId: self.form.recordId,
          nums: self.form.nums,
          // warehouse: self.form.warehouse,
          userId: self.form.userId,
          managerId: sessionStorage.getItem("userId"),
          deviceJson: self.form.deviceJson
        },
        {
          headers: {
            token: sessionStorage.getItem("TOKEN")
          }
        }
      )
      .then(data => {
        if (data.data.status == 2000) {
          // 
          self.$message({
            message: "",
            type: "success"
          });
          self.stepNum = 2;
          self.isStep2 = false;
          self.isStep3 = true;
          self.complete = true;
          this.btnLoading = false;
        }
        else {
          // 
          self.$message({
            message: "",
            type: "warning"
          });
          self.stepNum = 2;
          self.isStep2 = false;
          self.isStep3 = true;
          self.complete = false;
          if (data.data.status == 5004) {
            let msg = data.data.wrongSn.replace(/,/g, '');
            msg = msg.substring(0, msg.length-1) + ""
            self.errorMsg = `[${data.data.status}] SnSN${msg}`
          }
          else if (data.data.status == 5003) {
            let msg = data.data.wrongSn.replace(/,/g, '');
            msg = msg.substring(0, msg.length-1) + ""
            self.errorMsg = `[${data.data.status}] SN${msg}`
          }
          else if (data.data.status == 5002) {
            let msg = data.data.wrongList.replace(/;/g, '');
            msg = msg.substring(0, msg.length-1) + ""
            self.errorMsg = `[${data.data.status}] ${data.data.result}${msg}`
          }
          else if (data.data.status == 5001) {
            self.errorMsg = data.data.result
          }
          else {
            self.errorMsg = ""
          }
          this.btnLoading = false;
        }
        // 
        self.form = {
          company: '',
          recordId: '',
          nums: '',
          DemandList: [],
          userId: '',
          deviceJson: [],
        }
      })
      .catch(err => {
        this.btnLoading = false;
        console.log(err);
        if (err.status == 403) {
          sessionStorage.removeItem("company")
          sessionStorage.removeItem("TOKEN")
          sessionStorage.removeItem("username")
          sessionStorage.removeItem("userId")
          sessionStorage.removeItem("nickName")
          sessionStorage.removeItem("userType")
          sessionStorage.removeItem("email")
          self.$router.replace({ path: "/login" });
          self.$message({
            message: '',
            type: "warning"
          });
        } else {
          self.$message.error(err);
        }
      });
    },
    //  
    handleChange(file, fileList){
      this.fileTemp = file.raw;
      // console.log(fileList)
      if(this.fileTemp){
        if((this.fileTemp.type == 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet') 
          || (this.fileTemp.type == 'application/vnd.ms-excel')){
          this.importfxx(this.fileTemp);
        } else {
          this.$message({
            type:'warning',
            message:''
          })
        }
      } else {
        this.$message({
          type:'warning',
          message:''
        })
      }
    },
    //
    handleExceed(){
      this.$message({
        type:'warning',
        message:''
      })
      return;
    },
    //
    handleRemove(file,fileList){
      this.fileTemp = null
      // console.log(file,fileList)
      this.form.deviceJson = [] // 
      this.form.cnt = 0;
    },
    // excel
    importfxx(obj) {
      // console.log(obj)
      let _this = this;
      // let inputDOM = this.$refs.inputer;
      // DOM
      this.file = event.currentTarget.files[0];
      var rABS = false; //
      var f = this.file;
      var reader = new FileReader();
      //if (!FileReader.prototype.readAsBinaryString) {
      FileReader.prototype.readAsBinaryString = function(f) {
        var binary = "";
        var rABS = false; //
        // var pt = this;
        var wb; //
        var outdata;
        var reader = new FileReader();
        reader.onload = function(e) {
          // console.log(e)
          var bytes = new Uint8Array(reader.result);
          var length = bytes.byteLength;
          for (var i = 0; i < length; i++) {
              binary += String.fromCharCode(bytes[i]);
          }
          //excel
          var XLSX = require("xlsx");
          if (rABS) {
              wb = XLSX.read(btoa(binary), {
              //
              type: "base64"
              });
          } else {
              wb = XLSX.read(binary, {
              type: "binary"
              });
          }
          outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]); 
          //outdata
          //
          let arr = [], errCnt = 0;
          outdata.map(v => {
            let obj = {}
            obj.sn = v['']
            obj.model = v['']
            obj.spec = v['']
            obj.order = v['']
            obj.caseId = v['']
            obj.mac = v['MAC']
            obj.producer = v['']
            obj.sensor = v['']
            obj.version1 = v['1']
            obj.version2 = v['2']
            obj.version3 = v['3']
            obj.manufacturer = v['']
            obj.guarantee = _this.formatDate(v[''])
            obj.resolution = v['']
            obj.addressee = v['']
            obj.address = v['']
            obj.remarks = v['']
            /* SN */
            let flag = false
            for (let i = 0; i < arr.length; i++) {
              if (arr[i].sn == obj.sn) {
                flag = true;
                break;
              }
            }
            if (flag) {
              errCnt++;
            }
            /* 12SN */
            else if (/^\d{12}$/.test(obj.sn)) {
              arr.push(obj)
            }
            else {
              errCnt++;
            }
          });
          if (errCnt) {
            _this.$message({
              message: `excel${errCnt}`,
              type: "warning"
            })
          }
          _this.form.cnt = arr.length;
          _this.form.deviceJson = arr;
          // _this.da=arr;
          // _this.dalen=arr.length;
          // console.log(arr)
          return arr;
        };
        reader.readAsArrayBuffer(f);
      };
      if (rABS) {
        reader.readAsArrayBuffer(f);
      } else {
        reader.readAsBinaryString(f);
      }
    },
    manualVisible() {
      this.dialogFormVisible = true;
      this.dataTemp = {
        sn: null,
        model: "",
        spec: "",
        order: "",
        caseId: "",
        mac: "",
        producer: "",
        sensor: "",
        version1: "",
        version2: "",
        version3: "",
        manufacturer: "",
        guarantee: "",
        resolution: "",
        addressee: "",
        address: "",
        remarks: null
      }
    },
    handleAdd() {
      this.dialogFormVisible = false;
      this.form.deviceJson.push(this.dataTemp);
      this.form.cnt++;
    },
    handleDelete(index) {
      this.form.deviceJson.splice(index, 1);
      this.form.cnt--;
    },
    back0() {
      this.stepNum = 0;
      this.isStep2 = false;
      this.isStep1 = true;
      this.complete = false;
      this.reload();
    },
    back() {
      this.stepNum = 0;
      this.isStep3 = false;
      this.isStep1 = true;
      this.complete = false;
      this.reload();
    },
    //timeValueexcel
    formatDate(timeValue) {
      let time = new Date((timeValue- 1) * 24 * 3600000 + 1)
      time.setYear(time.getFullYear() - 70)
      let year = time.getFullYear() + ''
      let month = time.getMonth() + 1 + ''
      let date = time.getDate() + ''
      return year + "-" + month + "-" + date
    },
  }
}
</script>

